$font-url: 'https://fonts.googleapis.com/css?family=Nunito:400,700&subset=latin' !default;
$font-name: 'Nunito' !default;
$font-family: $font-name, 'Helvetica Neue', Arial, Helvetica, sans-serif !default;

@import "../../node_modules/semantic-ui-sass/scss/globals/_all";
@import "../../node_modules/semantic-ui-sass/scss/collections/_grid";
@import "../../node_modules/semantic-ui-sass/scss/elements/_segment";
@import "../../node_modules/semantic-ui-sass/scss/elements/_header";

html {
  background: url(data:image/svg+xml;base64,) no-repeat center center fixed;
  background-size: cover;
  height: 100%;
  width: 100%;
}

body {
  background-color: transparent;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.ui.grid {
  .ui.grid {
    position: relative;
    top: -10%;
  }

  div.twelve:first-child {
    padding-bottom: 0 !important;
    height: auto;
  }

  div.twelve.wide.column {
    padding-top: 0 !important;
    border: 0;
  }
}

h1.center.aligned {
  font-size: 2.2rem;
  border-radius: 10px;
  color: white;

  &.attached {
    border: 0;
    background-color: #383333;
  }
}

main {
  box-shadow: 0 0 1px -2px #383333;
  position: relative;
  transition: flex 1s;

  .ui.attached.segment {
    font-size: 1.5rem;
    color: #383333;
    border: 0;

    .wi-main {
      font-size: 8rem;
      margin-top: 10px;
      padding-bottom: 20px;
    }
  }

  #info-main .row {
    padding: 10px 0 0;
    margin-bottom: -10px;
  }

  #refresh {
    font-size: 1.9rem;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 28px;
    height: 28px;

    &:hover {
      cursor: pointer;
    }

    &:hover,
    &:active,
    &:focus {
      box-shadow: 0 0 1px 0 rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      outline: 0;
    }
  }
}

#info-main {
  #temperature {
    font-size: 4rem;
    height: 58px;
    letter-spacing: -3px;
    line-height: 1.2;
    margin-left: -8px;
  }

  #date {
    height: 66px;
    padding: 3px 5px 5px 4px;
    margin-left: -4px;
    background-color: white;
    margin-right: -21px;
    letter-spacing: -1px;
    font-size: 1.6rem;
  }

  .row:last-child {
    padding-top: 0;
    margin-bottom: 5px;
  }
}

#info-aux {
  border: 0;
  background-color: #383333;
  color: white;
  max-height: 0;

  & .basic {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.2s, visibility 0.2s;
  }

  &.visible,
  & .visible {
    max-height: 200px;
    visibility: visible;
    opacity: 1;
  }

  p {
    color: white;
    font-size: 1.5rem;
  }

  i {
    display: block;
    margin-bottom: 5px;
  }
}

.newline {
  display: block;
}

@media screen and (max-width: 420px) {
  .ui.grid div.twelve.wide.column {
    width: 100vw !important;
  }
}

@media screen and (min-width: 768px) {
  .ui.grid div.twelve.wide.column {
    max-width: 50% !important;
  }
}

@media screen and (min-width: 1200px) {
  .ui.grid div.twelve.wide.column {
    max-width: 30% !important;
  }
}



